<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs10 md6>
        <v-card>
          <v-card-text class="px2">
            <div ref="sale" style="width: 100%;height:350px"></div>
          </v-card-text>
        </v-card>
      </v-flex>

      <v-flex xs10 md6>
        <v-card >
          <v-card-text class="px2">
            <div ref="pie" style="width: 100%;height:350px"></div>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  //  ECharts 
  var echarts = require('echarts/lib/echarts');
  require('echarts/lib/chart/bar');
  require('echarts/lib/chart/pie');

  export default {
    name: "dashboard",
    data(){
      return {
        saleDate:[],
        saleSum:[],
        pieData:[]
      }
    },
    created(){

    },
    mounted(){
      this.$http.post("/Statistics/orderStatistics")
        .then((resp)=>{
          this.saleDate=resp.data.date;
          this.saleSum=resp.data.sum;
          for(let i=0;i<this.saleDate.length;i++)
            this.pieData.push({value:this.saleSum[i], name:this.saleDate[i]});
          this.setStatistics();
        })
        .catch((error)=>{
          console.log(error);
        })


    },
    methods:{
      setStatistics:function() {
        this.$nextTick(() => {
          var sale = echarts.init(this.$refs.sale);
          console.log(this.saleDate);
          // 
          var option = {
            title: {
              text: ''
            },
            tooltip: {},
            legend: {
              data:['']
            },
            xAxis: {
              data: this.saleDate
            },
            yAxis: {},
            series: [{
              name: '',
              type: 'bar',
              data: this.saleSum
            }]
          };

          // 
          sale.setOption(option);

          const pie = echarts.init(this.$refs.pie);

          pie.setOption({
            roseType: 'angle',
            title: {
              text: ''
            },
            series : [
              {
                name: '',
                type: 'pie',
                radius: '55%',
                data:this.pieData
              }
            ],
            itemStyle: {
              emphasis: {
                // 
                shadowBlur: 200,
                // 
                shadowOffsetX: 0,
                // 
                shadowOffsetY: 0,
                // 
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          })
        })

      }
    }
  }
</script>

<style scoped>

</style>



